import React, { useRef, useState, useEffect } from "react"
import mapboxgl from "!mapbox-gl"

const Map3D = () => {
    mapboxgl.accessToken = 'pk.eyJ1Ijoia2FsdmlzYW4iLCJhIjoiY2xhYXdhcHQ4MDcweTNvbjFueGw4NHlsciJ9.c18OWa3v2R-s_g6jiIF5Hw'

    const mapContainer = useRef(null)
    const longitude = 24.130012362878453
    const latitude = 56.96498776289796

    const flyFrom = {
        center: [longitude, latitude],
        zoom: 10,
        pitch: 0
    }
    const flyToTarget = {
        center: [longitude, latitude],
        zoom: 17.1,
        pitch: 50
    }

    useEffect(() => {
        const map = new mapboxgl.Map({
            container: mapContainer.current,
            style: 'mapbox://styles/kalvisan/clagpkwh4006x14nsmjwnutwc',  //from Mapbox Studio used style is "KALMARS Website-used"
            ...flyFrom
        })

        // Location marker with custom image (in _map3D.scss change location to image and adapt styling if needed)
        const mark = document.createElement('div')
        mark.className = 'location-marker'
        const newMArker = new mapboxgl.Marker({ element: mark, anchor: 'bottom' })
            .setLngLat([longitude, latitude])
            .addTo(map)

        map.on('load', () => {
            //Fly to Marker location
            map.flyTo({
                ...flyToTarget,
                duration: 7000,
                essential: true
            })
        })
    }, [])


    return (
        <div className="css--map-3d h-96" ref={mapContainer}></div>
    )
}

export default Map3D